
body,h1,h2,h3,h4,h5,h6,hr,p,blockquote,dl,dt,dd,ul,ol,li,pre,form,fieldset,legend,button,input,textarea,th,td{margin:0;padding:0
}
body,button,input,select,textarea{font:12px/1.5tahoma, arial, \5b8b\4f53
}
h1,h2,h3,h4,h5,h6{font-size:100%
}
address,cite,dfn,em,var{font-style:normal
}
code,kbd,pre,samp{font-family:couriernew, courier, monospace
}
small{font-size:12px
}
ul,ol{list-style:none
}
a{text-decoration:none
}
a:hover{text-decoration:none
}
sup{vertical-align:text-top
}
sub{vertical-align:text-bottom
}
legend{color:#000
}
fieldset,img{border:0
}
button,input,select,textarea{font-size:100%
}
table{border-collapse:collapse;border-spacing:0
}
*{outline:0;-webkit-text-size-adjust:none;-webkit-appearance:none;-webkit-tap-highlight-color:transparent
}
html,body{height:100%;min-height:100%
}
body{font-family:"PingFangSC-Regular", "Helvetica Neue", "Helvetica", "Arial", "sans-serif";-webkit-user-select:none;-webkit-text-size-adjust:none;-webkit-appearance:none;-webkit-user-drag:none;-webkit-overflow-scrolling:touch
}
input::-webkit-inner-spin-button,input::-webkit-outer-spin-button,input[type="number"],input[type="text"]{-webkit-appearance:none
}
a,img{-webkit-touch-callout:none
}
body{background:#f5f5f5;font-size:13px
}
.bg-white{background:#fff
}



